var getRmb=require('../../common/requset.js')
export default {
    data(){
        return {
            remark:'',
            type:'买入',
            state:'',
            price:'',
            rmb:'',
            fanta:'',
            ig:'',
            frte:true,
            checkall:'展示完整信息'
        }
    },
    onLoad(option) {
        var that = this
        //获取上个页面携带的数据，并复制
        var fanta = JSON.parse(option.main)
        that.fanta = fanta
        var ig = that.fanta[1]
        that.ig = that.fanta[1]
        that.state = fanta[0][0].order_status
        if(that.state == 102) {
            that.state = '待确认'
        }
        that.remark = fanta[0][0]
        that.price = that.remark.sale_money
        that.rmb = getRmb.getrmb(that.price)
    },
    methods:{
        btn:function () {
            var that = this
            that.frte=!that.frte;
            if(that.frte==false){
                that.checkall='收起';
            }else{
                that.checkall='展示完整信息'
            }
        }
    }
}
